    public componentDidMount(): void {
        this.tickInterval = setInterval(this.tick, 100);
        const query = getQuery();
        this.loadGameId = query.gameId;
        this.getToken();
    }

    public componentDidUpdate(prevProps: any, prevState: any) {
        //console.log({componentDidUpdate: explainDiff(diff.diff(prevState, this.state))});
        if (this.myPlayer && localStorage.getItem(PLAYER_NAME_KEY) !== this.myPlayer.name) {
            localStorage.setItem(PLAYER_NAME_KEY, this.myPlayer.name);
        }
        if (!this.auth) {
            return;
        }
        this.ensureParticipant();
        if (this.state.reordered.length > 0 && (this.state.board.find((e, i) => this.state.original[i] !== e) === undefined)) {
            this.setState({reordered: [], scooched: undefined});
        } else {
            if (this.pushImmediate) {
                clearTimeout(this.pushImmediate);
            }
            this.pushImmediate = setTimeout(this.push, 10);
        }
        if (this.state.baton) {
            if (!this.batonTimeout) {
                this.batonTimeout = setTimeout(() => this.setState((state) => {
                    this.batonTimeout = undefined;
                    if (state.baton && state.baton.timeout < Date.now()) {
                        return {baton: undefined};
                    }
                    return {};
                }), this.state.baton.timeout - Date.now());
            }
        }
        // Ensure that all participants have the same URL in their window so reloading doesn't start a new game:
        if (this.state.options.solo !== undefined) {
            if (window.location.href !== this.gameUrl) {
                if (window.history.pushState) {
                    window.history.pushState({path: this.gameUrl}, "", this.gameUrl);
                }
            }
        }
        sessionStorage.setItem("uiState", JSON.stringify(this.state.uiState));
    }

    public componentWillUnmount(): void {
        if (this.batonTimeout) {
            clearTimeout(this.batonTimeout);
        }
        if (this.tickInterval) {
            clearInterval(this.tickInterval);
        }
        if (this.pollInterval) {
            clearInterval(this.pollInterval);
        }
        if (this.automateInterval) {
            clearInterval(this.automateInterval);
        }
    }

