function ImageWithDisabledState(props: {
    src: string,
    onClick: () => void,
    title?: string,
    disabled: boolean,
    action: GameAction,
}) {
    const {action, disabled} = props;
    let onClick: undefined | (() => void) = props.onClick;
    if (disabled) {
        onClick = undefined;
    }
    return <Row className={classNames(disabled && "disabled", `action-${action}`)} onClick={onClick}>
        <div className="image-and-label">
            <Image {...props}/>
            <div className={"image-label-container"}>
                <div className={"image-label"}>{action}</div>
            </div>
        </div>
    </Row>;
}

// Cache of generated json data URLs
const JSON_TO_URL_DATA: { [key: string]: string } = {};

function jsonToUrl(key: string, stuff: any) {
    const data = new Blob([JSON.stringify(stuff)], {type: "application/json"});

    // If we are replacing a previously generated file we need to
    // manually revoke the object URL to avoid memory leaks.
    if (JSON_TO_URL_DATA[key]) {
        window.URL.revokeObjectURL(JSON_TO_URL_DATA[key]);
    }

    JSON_TO_URL_DATA[key] = window.URL.createObjectURL(data);

    // returns a URL you can use as a href
    return JSON_TO_URL_DATA[key];
}

