    private renderEmailDialog = () => {
        const {showEmailDialog, emailLink, jsonLink, gameDataBlob} = this.state;
        if (!showEmailDialog) {
            return;
        }
        return <Modal show={showEmailDialog} onHide={this.closeEmailDialog} centered={true}>
            <Modal.Header closeButton={true}>
                <Modal.Title>Scooch Feedback</Modal.Title>
            </Modal.Header>

            <Modal.Body>
                <p>
                    Thanks for playing! We love feedback, so let us know what you like (or don't like) about our game.
                    If you ran into a bug, let us know about it, and anything you think might trigger it.
                </p>
                <p>
                    You may also <a href={jsonLink} download={true}>download your game data</a>.
                </p>
            </Modal.Body>

            <Modal.Footer>
                <Col className="d-flex justify-content-center">
                    <a href={emailLink} className="btn btn-primary">Send Email</a>
                </Col>
            </Modal.Footer>
        </Modal>;
    }

    private openEmailDialog = () => {
        const {gameState} = this.props;
        const gameData = gameState();
        const gameDataBlob = JSON.stringify(gameData, null, 2);
        const emailTemplate = {
            scheme: "mailto",
            to: ["jpollak@zynga.com"],
            subject: "Scooch Feedback",
            body: `

This is the state of my game:
${gameDataBlob}
`,
        };
        this.setState({
            showEmailDialog: true,
            gameDataBlob,
            emailLink: URI.serialize(emailTemplate),
            jsonLink: jsonToUrl("emailDialog", gameData),
        });
    }

    private closeEmailDialog = () => {
        this.setState({
            showEmailDialog: false,
            gameDataBlob: undefined,
            emailLink: undefined,
            jsonLink: undefined,
        });
    }
